import runWaterForm from "./waterForm/main.js";
import generateResult from "./utils/calculateResult.js";

runWaterForm();

const generateIngest = (container) => {
  const ingest = document.createElement("div");
  ingest.classList.add("ingest");
  ingest.classList.add("entry-data");

  ingest.innerHTML = `  
    <div class="input input-amount">
        <label>Cantidad<span>(Tazas)</span></label>
        <input type="text" />
    </div>
    <div class="input select-ingest">
        <label>Ingesta</label>
        <select>
            <option>Papaya</option>
        </select>
    </div>
    <div class="water-equivalent entry-result">
        <p>Cantidad de Agua</p>
        <p>200 mL</p>
    </div>
    `;

  container.appendChild(ingest);
};

const ingestContainer = document.querySelector(".ingest-container");
const btnAddIngest = document.querySelector(".add-ingest");
btnAddIngest.addEventListener("click", (e) => {
  e.preventDefault();
  generateIngest(ingestContainer);
});

const ingestForm = document.querySelector(".form-ingest");

const appendBtnsIngest = document.querySelector(".apend-ingest");

const btnCalculateIngest = document.querySelector(".calculate-ingest");
btnCalculateIngest.addEventListener("click", (e) => {
  e.preventDefault();
  appendBtnsIngest.remove();
  generateResult("ingestResult", ingestForm);
});
